// ISR Configuration
export const revalidate = 3600; // Revalidate every hour
export const dynamicParams = true; // Allow dynamic job IDs

// Dynamic metadata for analysis pages
export async function generateMetadata({ params }) {
  const resolvedParams = await params;
  const jobId = resolvedParams.jobId;
  
  return {
    title: `Analys #${jobId} | SEO Analyzer`,
    description: `Se resultatet av din SEO-, Lighthouse- eller Crawl-analys. Detaljerade rapporter och rekommendationer fr frbttring.`,
    robots: "noindex, nofollow", // Analysis pages should not be indexed
    openGraph: {
      title: `Analys #${jobId} | SEO Analyzer`,
      description: "Se resultatet av din webbplatsanalys med detaljerade rapporter och rekommendationer.",
      type: "website",
    },
  };
}

// Import client component
import AnalysisPageClient from './AnalysisPageClient';
import { headers } from 'next/headers';

export default async function AnalysisPage({ params }) {
  const ffPriority = (process.env.NEXT_PUBLIC_FEATURE_LH_PRIORITY === 'true');
  const ffDebug    = (process.env.NEXT_PUBLIC_DEBUG_PROBES === 'true');

  // Bygg absolut bas-URL frn inkommande request-headrar (fungerar bakom proxy/CDN)
  const hdrs = headers();
  const host = hdrs.get('x-forwarded-host') || hdrs.get('host');
  const proto = hdrs.get('x-forwarded-proto') || 'http';
  const absoluteBase = `${proto}://${host}`;

  // Hjlpare: enkel ULID-kontroll (26 tecken, Crockford base32)
  const isUlid = (s) => typeof s === 'string' && /^[0-9A-HJKMNP-TV-Z]{26}$/.test(s);
  let resolvedId = params?.jobId;

  // Numeriskt "vnligt" ID? -> mappa till ULID via job-status
  if (!isUlid(resolvedId) && /^\d+$/.test(String(resolvedId))) {
    const jobRes = await fetch(`${absoluteBase}/api/job/${resolvedId}/status`, { cache: 'no-store' });
    if (jobRes.ok) { const job = await jobRes.json(); if (isUlid(job?.resultId)) resolvedId = job.resultId; }
  }

  // Hmta analysen server-side fr att faststlla analysisType (SSR-bevis)
  let analysisType = 'unknown';
  let ssrHasResults = false;
  let initialResults = null;
  try {
    const base = process.env.NEXT_PUBLIC_BASE_URL ?? absoluteBase;
    const res = await fetch(`${base}/api/v1/analyses/${resolvedId}`, { cache: 'no-store' });
    if (res.ok) {
      const data = await res.json();
      analysisType = data?.type || 'unknown';
      ssrHasResults = (data?.status === 'completed') && !!(data?.summary || data?.results);
      // Robust pick av rtt payload per typ (hanterar olika API-format)
      const pick = (...candidates) =>
        candidates.find(v => {
          if (!v) return false;
          if (v === null || v === undefined) return false;
          if (Array.isArray(v)) return v.length > 0;
          if (typeof v === 'object') {
            // Acceptera objekt med nycklar ELLER objekt med vissa frvntade crawl-flt
            const keys = Object.keys(v);
            return keys.length > 0 || !!(v.pages || v.summary || v.stats || v.results);
          }
          return !!v;
        }) || null;

      if (analysisType === 'lighthouse') {
        initialResults = pick(
          data?.summary,
          data?.lighthouseResults,
          data?.results?.lighthouse,
          data?.data?.summary,
          data?.payload?.summary,
          data?.report?.summary,
          data?.data
        );
      } else if (analysisType === 'seo') {
        initialResults = pick(
          data?.summary,
          data?.results,
          data?.seoResults,
          data?.data?.results,
          data?.payload?.results,
          data?.report?.results,
          data?.data
        );
      } else if (analysisType === 'crawl') {
        const crawlSummary = pick(
          // vanligast fr crawl hos dig: summary med pages/stats
          data?.summary,
          data?.data?.summary,
          data?.payload?.summary,
          data?.report?.summary,
          // alternativa flt
          data?.results,
          data?.crawlResults,
          data?.data?.results,
          data?.payload?.results,
          data?.report?.results,
          data?.data
        );
        // Normalisera formen s klienten alltid fr { summary: ... }
        initialResults = (crawlSummary && typeof crawlSummary === 'object')
          ? { summary: crawlSummary }
          : crawlSummary;
        // Fallback: hmta alltid list-endpointen och skriv ver om den har rikare summary
        try {
          const listRes = await fetch(`${absoluteBase}/api/v1/analyses`, { cache: 'no-store' });
          if (listRes.ok) {
            const listJson = await listRes.json();
            const found = (listJson?.analyses || []).find(a => a?.id === params?.jobId);
            const candidate = found?.summary || found?.results || null;
            if (candidate && typeof candidate === 'object' && Object.keys(candidate).length > 0) {
              initialResults = { summary: candidate };
              console.log('[SSR] crawl: using list fallback for %s, keys=%o', params?.jobId, Object.keys(initialResults.summary || {}));
            } else {
              console.log('[SSR] crawl: list fallback had no richer payload for %s', params?.jobId);
            }
          } else {
            console.log('[SSR] crawl: list fallback HTTP %s for %s', listRes.status, params?.jobId);
          }
        } catch (e) {
          console.log('[SSR] crawl: list fallback error for %s -> %s', params?.jobId, e?.message || e);
        }
        // Stt ssrHasResults efter normaliserad crawl-struct
        {
          const crawlObj  = (initialResults && initialResults.summary) ? initialResults.summary : initialResults;
          const crawlKeys = (crawlObj && typeof crawlObj === 'object') ? Object.keys(crawlObj) : [];
          const pagesLen  = Array.isArray(crawlObj?.pages) ? crawlObj.pages.length : 0;
          ssrHasResults   = (pagesLen > 0) || (crawlKeys.length > 0);
        }
        // DEBUG (bara om ANALYSIS_DEBUG=1)
        if (process.env.ANALYSIS_DEBUG === '1') {
          try {
            const dbgObj = (initialResults && initialResults.summary) ? initialResults.summary : initialResults;
            console.log(
              '[SSR] /analys/%s type=%s ssrHasResults=%s keys=%o',
              params?.jobId,
              analysisType,
              ssrHasResults,
              Object.keys(dbgObj || {})
            );
          } catch {}
        }
      } else {
        initialResults = pick(
          data?.results,
          data?.summary,
          data?.data?.results,
          data?.data?.summary,
          data?.payload?.results,
          data?.payload?.summary,
          data?.report?.results,
          data?.report?.summary,
          data?.data
        );
      }

      //  Om inget hittades men vi har ett resultId: hmta resultaten direkt
      if (!initialResults) {
        const rid =
          data?.resultId || data?.resultsId || data?.result?.id || data?.result?.resultId;
        if (rid) {
          const res2 = await fetch(`${base}/api/v1/results/${rid}`, { cache: 'no-store' });
          if (res2.ok) {
            const rdata = await res2.json();
            if (analysisType === 'lighthouse') {
              initialResults =
                rdata?.summary ||
                rdata?.lighthouse ||
                rdata?.lighthouseResults ||
                rdata;
            } else if (analysisType === 'seo') {
              initialResults =
                rdata?.results ||
                rdata?.seoResults ||
                rdata?.data?.results ||
                rdata;
            } else if (analysisType === 'crawl') {
              initialResults =
                rdata?.results ||
                rdata?.crawlResults ||
                rdata?.data?.results ||
                rdata;
            } else {
              initialResults =
                rdata?.results ||
                rdata?.summary ||
                rdata;
            }
          }
        }
      }
    }
  } catch (e) {
    // lmna analysisType = 'unknown' vid fel
  }

  return (
    <>
      {ffDebug && (
        <>
          {/* SSR env-probe (env) */}
          <div data-testid="ssr-ff-lh-priority" data-val={String(ffPriority)} hidden />
          {/* SSR probe (prop)  visar att prop skickas vidare */}
          <div data-testid="ssr-ff-lh-priority-prop" data-val={String(ffPriority)} hidden />
          {/* SSR probe  vilken vy sger backendet att detta r? */}
          <div data-testid="ssr-analysis-type" data-val={String(analysisType)} hidden />
          {/* SSR probe  har results redan funnits p serverns svar? */}
          <div data-testid="ssr-has-results" data-val={String(ssrHasResults)} hidden />
          {/* Skicka ven analysisType som prop (serverklient), som debug-eko */}
          <div data-testid="ssr-analysis-type-prop" data-val={String(analysisType)} hidden />
        </>
      )}
      <AnalysisPageClient
        params={params}
        ffPriority={ffPriority}
        ssrAnalysisType={analysisType}
        ssrHasResults={ssrHasResults}
        initialResults={initialResults}
      />
    </>
  );
}